import React, { useState, useEffect, useRef }  from 'react';
import { Container, Form } from 'react-bootstrap';

import { create } from 'ipfs-http-client';

export default ()=> {
  const client = create('https://ipfs.infura.io:5001/api/v0')
  const [file, setFile ] = useState(null);
  const [fileDataURL, setFileDataURL] = useState(null);
  const [ipfsPath, setIpfsPath] = useState('');

  const inputFileHandle = useRef(null);

  const  uploadVideo = async (e) => {
    
    const files = e.target.files;
    const file = files[0];
    setFile(file);

    return true;
  }

  const addFile = () => {
    setFile(null);
    inputFileHandle.current.click();
  }

  useEffect(() => {
    let fileReader, isCancel = false;
    if (file) {
      fileReader = new FileReader();
      fileReader.onload = (e) => {
        const { result } = e.target;
        if (result && !isCancel) {
          setFileDataURL(result)
        }
      }
      fileReader.readAsDataURL(file);
    }
    return () => {
      isCancel = true;
      if (fileReader && fileReader.readyState === 1) {
        fileReader.abort();
      }
    }

  }, [file]);

  const uploadEntrypoint =(comp)=> <Container fluid={true} className="m-0 p-0"  onClick={addFile} >
        <Form.Control type="file" hidden={true} ref={inputFileHandle} onChange={uploadVideo}  multiple={false}
          accept={'image/*'}/>
      {comp}
    </Container>

  const ipfsImageUpload = async ()=> {
    return new Promise(async resolve => {
      if (file) {
        const added = await client.add(file);
        added.originFileName = file.name;
        setIpfsPath(added.path);
        resolve(added.path);
        
      } else {
        resolve(false);
      }
    });
  }
  const ipfsUpload = async (data)=> {
      return new Promise(async resolve => {
        const added = await client.add(JSON.stringify(data));
        resolve(added.path);
      });
  }
  const showIpfs =()=> !!ipfsPath && <span>{ipfsPath}
    <img src={`https://ipfs.infura.io/ipfs/${ipfsPath}`} alt="preview" style={{width:'10rem'}} />
  </span>

  return { uploadEntrypoint, showIpfs, ipfsUpload, ipfsImageUpload, fileDataURL  }
}